import React from "react";

export const Sticker = (props = {}) => {
    if (props.sticker) {
        const stickerStyle = {
            background: {backgroundColor: "#" + props.sticker.color || "#FFD200"},
            font: {
                color: "#" + props.sticker.textColor || "#000000",
                fontSize: props.sticker.textSize || "small",
            },
        };
        const stickerAlignStyle = props.sticker.align === "LEFT" ? "u-left u-position-left" : "u-right u-position-right";
        const className = props.className ? props.className : "";
        const htmlStickerName = {__html: props.sticker.name};
        if (props.renderMobileVersion) {
            return (
                <div className={`opl-glorious--badges opl-glorious--badges-small  ${stickerAlignStyle}`}>
                    <span className="opl-glorious--badges-bg u-margin-left-l u-medium-margin-left-xl u-small-no-margin"
                        style={stickerStyle.background}/>
                    <div
                        className="opl-glorious--badges-text u-margin-left-l u-medium-margin-left-m u-medium-margin-left-xl u-small-no-margin" style={stickerStyle.font}>
                        <p className="u-font-bold u-small-font-small u-no-margin-b" dangerouslySetInnerHTML={htmlStickerName}/>
                    </div>
                </div>
            );
        } else {
            return (
                <div className={`opl-glorious--badges opl-glorious--badges-small u-large-margin-left-m u-large-margin-left-l ${className} ${stickerAlignStyle}`} >
                    <span className="opl-glorious--badges-bg u-small-no-margin"
                        style={stickerStyle.background}/>
                    <div className="opl-glorious--badges-text " style={stickerStyle.font}>
                        <p className={"u-font-bold u-small-font-small u-no-margin-b"} dangerouslySetInnerHTML={htmlStickerName}/>
                    </div>
                </div>
            );
        }
    } else return null;
};
